@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400;500;700&display=swap');

.outer_header{
    width: 100%;
    height: 80px;
    min-width: 380px;
    display: flex;
    justify-content: center;
}
.inner_header{
    width: 85%;
    height: 100%;
    display: flex;
    justify-content: space-between;
}
.inner_selectors{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
  
}
.inner_selectors > div{
    width: 97px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items:center;
}
.inner_selectors > div >h1 > a{
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
}

.inner_selectors > div > h1 > a:hover {
    color: rgba(94, 206, 123, 1);
    border-bottom: 2px solid rgba(94, 206, 123, 1);
    cursor: pointer;
    padding: 0 0 32px 0;
    font-weight: 600;
 }
.cat_selectors{
    width: 234px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo{
    width: 28.62px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cart_logo{
    margin: 0 10px 0 0;
}
.cart_menu{
    width: 234px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.cart_menu > div{
    width: 20px;
    
}
.cart_box{
    margin:0 0 0 22px;
    display: flex;
    justify-content: flex-end;
}
.cart_logo:hover{
    cursor: pointer;
}
#wheel{
    margin: 0 0 0 15px;
}
.arrow_box{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.arrow{
    display: flex;
    width: 20px;
    height: 15px;
    justify-content: flex-start;
    align-items: center;
}
.arrow_box:hover{
    cursor: pointer;
}
.curency_box:hover{
    cursor: pointer;
}
.curency_box > h1{
    font-size: 18px;
    font-family: 'Raleway', sans-serif ;
    font-weight: 300;
}
.dropdwn{
    width: 100px;
    position: absolute;
    margin:260px 0px 0px -37px;
    box-shadow: 0px 4px 35px rgba(168, 172, 176, 0.19);
}
.dropdwn > div > div{
    margin-top: 21px;
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin: 1px 0 0 2px;
}
.dropdwn > div > div > h1{
    font-size: 18px;
    font-family:'Raleway', sans-serif;
    text-align: center;
    font-weight: 300;
    margin: 10px 0 0px 0;
    height: 100%;
    width: 100%;
}
.dropdwn > div > div:hover{
    background-color: rgba(94, 206, 123, 1);
    color: white;
}
#hideMe{
    display: none;
}
.inner_selectors > div >h1 > a{
    text-decoration: none;
    color: black;

}
.inner_selectors > div >h1 > a:hover{
    color: rgba(94, 206, 123, 1);
}
.cart_count{
    
    position: absolute;
    margin: -18px 0 0 11px;
    font-size: 13px;
    text-align: center;
    color: white;
}
.cart_count > p{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    height: 19px;
    width: 19px;
    background-color: black;
}


@media only screen and (max-width: 1100px) {
  .inner_header{
    width: 100%;
    }
    .dropdwn{
        margin:260px 0 0 -48px;
        z-index: 2;
    }
    .cart_box >svg{
        margin:0 0 0 -15px;
    }
    .dropdwn >div{
        margin-left: 4px;
    }
    .logo{
        display:none;
    }
    .cart_dropdown{
        position: absolute;
        width: 325px;
        margin: 51px 1px 0 -289px ;
        
        }

  }














@media only screen and (max-width: 480px) {
    .inner_header{
        width: 95%;
    }
    .inner_selectors > div{
        width: 35%;
        margin: 0 11px 0 0;
    }
    .logo{
        display:none;
    }
    .inner_selectors > div >h1{
        font-size: 4vw;
        font-family: 'Raleway', sans-serif;
     }
     .dropdwn > div >h1{
         font-size: 13px;
     }
    
     .curency_box >h1{
         
         font-size: 18px;
     }
     .cart_menu > div{
         width: 15%;
     }
     .dropdwn > div{
         margin: 0px 0 3px -1px;
     }
     .cart_dropdown{
        position: absolute;
        width: 99%;
        margin: 51px 1px 0 -289px ;
       
        }
    .dropdwn{
        margin: 257px 0 0 -29px;
        width: 66px;
        height: auto;
    }
    .cart_logo{
        margin: 0 10px 0 0;
    }

}

@media only screen and (max-width: 380px) {
    .inner_header{
        width: 94%;
       
    }
}